import { useEffect, useState } from "react";
import "./App.css";
import Maze from "./components/NewMaze";
import Slider from "./components/Slider";
import styles from "./Maze.module.css";
function App() {
  const [mazeToggle, setMazetoggle] = useState(true);
  const [showControls, setShowControls] = useState(false);
  const [up, setUp] = useState(0);
  const [down, setDown] = useState(0);
  const [left, setLeft] = useState(0);
  const [right, setRight] = useState(0);
  const [numberOfPixel, setNumberOfPixel] = useState(6);
  const keyPressHandler = (e) => {
    const code = e.code;
    if (code === "KeyW") {
      setUp((state) => state + 1);
    } else if (code === "KeyD") {
      setRight((state) => state + 1);
    } else if (code === "KeyS") {
      setDown((state) => state + 1);
    } else if (code === "KeyA") {
      setLeft((state) => state + 1);
    }
  };
  useEffect(() => {
    console.log(numberOfPixel);
  }, [numberOfPixel]);
  useEffect(() => {
    if (showControls) {
      window.addEventListener("keypress", keyPressHandler);
    }
    return () => window.removeEventListener("keypress", keyPressHandler);
  }, [showControls]);
  return (
    <div className={styles.app} onKeyDown={(e) => console.log("ckjaj")}>
      <div className={styles.controls}>
        {showControls || true ? (
          <Slider
            min={4}
            max={30}
            value={numberOfPixel}
            onChange={(event) => setNumberOfPixel(+event.target.value)}
          />
        ) : null}
      </div>
      <div className={styles.maze}>
        <Maze
          numberOfPixel={numberOfPixel}
          pixelSize={24}
          wallSize={4}
          mazeState={mazeToggle}
          onEnd={(state) => setShowControls(state)}
          up={up}
          down={down}
          left={left}
          right={right}
        />
      </div>
      <div className={styles.movement}>
        {showControls ? (
          <div>
            <div className={styles.row}>
              <button
                className={styles.but}
                onClick={() => setUp((state) => state + 1)}
              >
                up
              </button>
            </div>
            <div className={styles.row}>
              <button
                className={styles.but}
                onClick={() => setLeft((state) => state + 1)}
              >
                left
              </button>
              <button
                className={styles.but}
                onClick={() => setDown((state) => state + 1)}
              >
                bottom
              </button>
              <button
                className={styles.but}
                onClick={() => setRight((state) => state + 1)}
              >
                right
              </button>
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
}

export default App;
