import styles from "./Slider.module.css";

const Slider = (props) => {
  return (
    <div className={styles.wrapper}>
      <h1>Maze size</h1>
      <h1 className={styles.text}>{props.value}</h1>
      <input
        type="range"
        min={props.min}
        max={props.max}
        value={props.value}
        onChange={props.onChange}
      />
    </div>
  );
};

export default Slider;
