import { useEffect, useRef, useState } from "react";
let Cells = [];
const NewMaze = (props) => {
  const [restart, setRestart] = useState(true);
  const [stack, setStack] = useState([]);
  const [current, setCurrent] = useState(null);
  const [numberOfVisited, setNumberOfVisited] = useState(0);
  const [tracked, setTracked] = useState([0]);
  const { numberOfPixel, pixelSize, wallSize } = props;
  const [mazeState, setMazeState] = useState(true);

  const canvasRef = useRef(null);
  useEffect(() => {
    console.log({ stack, restart, current, numberOfVisited, tracked });
  }, [mazeState]);
  useEffect(() => {
    return () => {
      if (canvasRef.current !== null) {
        const ctx = canvasRef.current.getContext("2d");
        setMazeState(() => false);
        setRestart(true);
        setStack([]);
        setCurrent(null);
        setNumberOfVisited(0);
        setTracked([0]);
        Cells = [];
        props.onEnd(false);
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      }
    };
  }, [numberOfPixel, pixelSize, wallSize]);
  function Cell(x, y) {
    this.x = x;
    this.y = y;
    this.rightWall = false;
    this.downWall = false;
    this.visited = false;
    this.display = () => {
      console.log(this.x, this.y, this.visited);
    };
    this.fillWalls = (side) => {
      const ctx = canvasRef.current.getContext("2d");
      ctx.fillStyle = "red";
      if (side === 1) {
        this.rightWall = true;
        ctx.fillRect(this.x + pixelSize, this.y, wallSize, pixelSize);
      } else {
        this.downWall = true;
        ctx.fillRect(this.x, this.y + pixelSize, pixelSize, wallSize);
      }
    };
  }
  useEffect(() => console.log(tracked), [tracked]);
  const findReverse = (index) => {
    const ele = tracked[tracked.length - 2],
      current = tracked[tracked.length - 1];
    console.log(index, ele, current);
    if (ele && ele === index) {
      console.log("reverse");
      const diff = current - ele;
      console.log(diff);
      diff > 0
        ? diff === numberOfPixel
          ? tracker(index, "red", true, false)
          : tracker(index, "red", true, true)
        : diff === -numberOfPixel
        ? tracker(index, "red", true, true)
        : tracker(index, "red", false, true);
      setTracked((state) => state.slice(0, state.length - 1));
      return false;
    }
    return true;
  };
  const tracker = (index, color, loc, dir) => {
    const ctx = canvasRef.current.getContext("2d");
    ctx.fillStyle = color;
    let x = Cells[index].x,
      y = Cells[index].y,
      width = pixelSize,
      height = pixelSize;
    if (loc) {
      if (dir) {
        x += pixelSize;
      } else {
        y += pixelSize;
      }
    }
    if (dir) {
      width += wallSize;
    } else {
      height += wallSize;
    }
    ctx.fillRect(x, y, width, height);
  };

  useEffect(() => {
    if (props.left !== 0) {
      const current = tracked[tracked.length - 1];
      if (
        current - 1 > 0 &&
        current % numberOfPixel !== 0 &&
        Cells[current - 1].rightWall &&
        findReverse(current - 1)
      ) {
        tracker(current - 1, "green", false, true);
        setTracked((state) => [...state, current - 1]);
      }
    }
  }, [props.left]);
  useEffect(() => {
    if (props.up !== 0) {
      console.log("up");
      const current = tracked[tracked.length - 1];
      if (
        current - numberOfPixel > 0 &&
        Cells[current - numberOfPixel].downWall &&
        findReverse(current - numberOfPixel)
      ) {
        tracker(current - numberOfPixel, "green", false, false);
        setTracked((state) => [...state, current - numberOfPixel]);
      }
    }
  }, [props.up]);

  useEffect(() => {
    if (props.right !== 0) {
      console.log("right");
      const current = tracked[tracked.length - 1];
      if (
        Cells[current].rightWall &&
        current + 1 < numberOfPixel ** 2 &&
        (current + 1) % numberOfPixel !== 0 &&
        findReverse(current + 1)
      ) {
        tracker(current, "green", true, true);
        setTracked((state) => [...state, current + 1]);
      }
    }
  }, [props.right]);
  useEffect(() => {
    if (props.down !== 0) {
      console.log("down");
      const current = tracked[tracked.length - 1];
      if (
        Cells[current].downWall &&
        current + numberOfPixel < numberOfPixel ** 2 &&
        findReverse(current + numberOfPixel)
      ) {
        tracker(current, "green", true, false);
        setTracked((state) => [...state, current + numberOfPixel]);
      }
    }
  }, [props.down]);
  const findNeighbours = (index) => {
    const neighbours = [];
    if (index - numberOfPixel > 0) {
      neighbours.push(index - numberOfPixel);
    }
    if (index + numberOfPixel < numberOfPixel ** 2) {
      neighbours.push(index + numberOfPixel);
    }
    if (index - 1 > 0 && index % numberOfPixel !== 0) {
      neighbours.push(index - 1);
    }
    if (index + 1 < numberOfPixel ** 2 && (index + 1) % numberOfPixel !== 0) {
      neighbours.push(index + 1);
    }
    return neighbours.filter((ele) => {
      if (Cells.length && ele) {
        return Cells[ele].visited !== true;
      }
      return true;
    });
  };
  const removeWalls = (current, next) => {
    const difference = next - current;
    if (difference === 1) {
      Cells[current].fillWalls(1);
    } else if (difference === -1) {
      Cells[next].fillWalls(1);
    } else if (difference > 0) {
      Cells[current].fillWalls(-1);
    } else {
      Cells[next].fillWalls(-1);
    }
  };
  const mazeController = () => {
    const ctx = canvasRef.current.getContext("2d");
    ctx.fillStyle = "green";
    ctx.fillRect(0, 0, pixelSize, pixelSize);
    ctx.fillStyle = "blue";
    ctx.fillRect(
      numberOfPixel * (wallSize + pixelSize) - pixelSize,
      numberOfPixel * (wallSize + pixelSize) - pixelSize,
      pixelSize,
      pixelSize
    );
  };
  const mazeGenerator = (index) => {
    const neighbours = findNeighbours(index);
    if (numberOfVisited === numberOfPixel ** 2 - 1) {
      console.log("end");
      mazeController();
      props.onEnd(true);
      return;
    }
    //STEP 2.1
    if (neighbours.length != 0) {
      //STEP 2.1.1
      const random = Math.floor(Math.random() * neighbours.length);
      const selected = neighbours[random];
      console.log(selected);
      //STEP 2.1.2
      setStack((state) => [...state, index]);
      //STEP 2.1.3
      removeWalls(index, selected);
      if (Cells[selected].visited === false) {
        setNumberOfVisited((state) => state + 1);
      }
      Cells[selected].visited = true;
      setTimeout(() => setCurrent(selected), 10);
    } else if (stack.length > 0) {
      const last = stack[stack.length - 1];
      const newStack = stack.slice(0, stack.length - 1);
      setStack((state) => newStack);
      setTimeout(() => setCurrent(last), 10);
    }
  };
  useEffect(() => {
    if (current !== null && mazeState) {
      mazeGenerator(current);
    }
  }, [current]);

  useEffect(() => {
    if (restart != null) {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext("2d");
      setRestart(null);
      ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      ctx.fillStyle = "#000000";
      ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);
      ctx.fillStyle = "red";
      const Pixels = [];
      let spaceH = 0,
        spaceW;
      while (spaceH < ctx.canvas.height) {
        spaceW = 0;
        while (spaceW < ctx.canvas.width) {
          ctx.fillRect(spaceW, spaceH, pixelSize, pixelSize);
          const cell = new Cell(spaceW, spaceH);
          Pixels.push(cell);
          spaceW += pixelSize + wallSize;
        }
        spaceH += pixelSize + wallSize;
      }
      Cells = [...Pixels];
      //STEP 1
      console.log(Cells.length, "initial", numberOfPixel);
      setMazeState(true);
      setNumberOfVisited(1);
      setCurrent(0);
    }
  }, [restart]);

  return (
    <canvas
      ref={canvasRef}
      width={(wallSize + pixelSize) * numberOfPixel - wallSize}
      height={(wallSize + pixelSize) * numberOfPixel - wallSize}
    />
  );
};

export default NewMaze;
